export const updateCandidate = async (reqData: any, candidateId: any) => {
  try {
	const response = await axios.put(`${apiurl}contacts/${candidateId}`, reqData, {
	  headers: {
		'Content-Type': 'application/json',
		'X-Requested-With': 'XMLHttpRequest',
	  },
	});
	return response.data;  // Axios automatically parses the JSON response
  } catch (err : any) {
	// Axios throws an error for non-2xx status codes, you can access `err.response`
	return err.response ? err.response.data : err.message;
  }
};

export const fetchCandidates = async () => {
	try {
		const response = await axios.get(`${apiurl}contacts`);
		return response.data;  // Axios automatically parses the JSON response
	} catch (err : any) {
		// Axios throws an error for non-2xx status codes, you can access `err.response`
		return err.response ? err.response.data : err.message;
	}
}

// Only Accessible for Super Admin
export const deleteCandidates =async (candidateId: any) => {
	try {
		const response = await axios.delete(`${apiurl}contacts/${candidateId}`);
		return response.data;  // Axios automatically parses the JSON response
	} catch (err : any) {
		// Axios throws an error for non-2xx status codes, you can access `err.response`
		return err.response ? err.response.data : err.message;
	}
}


